<template>
<div>
  <main>
    <section id="notice_whole">
      <div id="notice_banner"></div>
      <div id="breadcrumb_one">
        <a href="index.html">home</a>
        <a href=""></a>
      </div>
      <div id="notice">
        <h3 class="notice"></h3>
        <section id="m_2">
          <table>
            <tr class="notice_cap">
              <th>No</th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
            <tr v-for="noti in list" v-bind:key="noti.infoNum">
              <td>{{noti.infoNum}}</td>
              <td><a class="click" v-on:click="getone(noti.infoNum)">
                {{noti.infoTitle}}</a></td>
              <td></td>
              <td>{{getdate(noti.infoDate)}}</td>
              <td>{{noti.viewCount}}</td>
            </tr>
          </table>

          <div v-if="admin">
          <router-link to="/no-write" class="write"></router-link>
          </div>

        </section>
      </div>
    </section>
  </main>

</div>
</template>


<script>
export default {
  name: 'NoList',
  data(){
    return{
      list:[],
      admin:false
    };
  },

  created:function(){
    const self = this;

    var hascookie = self.$cookies.isKey('token');
    if(hascookie){
      if(self.$cookies.get('memnum') == 1)
      self.admin = true;
    }

    self.$axios.get('/notice')
    .then(function(resource){
      if(resource.data.result){
        self.list = resource.data.boardList;
      }
    }); // GET
  },

  methods:{
    getone: function(num){
      this.$router.push({
        name:'NoDetail',
        params:{
          num:num
        }
      });
    }, // getone:

    getdate: function(date){
      //   
      var year  = date.getFullYear();
      var month = ("0"+(1+date.getMonth())).slice(-2);
      var day   = ("0"+date.getDate()).slice(-2);

      var fdate = year + "" + month + "" + day + "";
      return fdate;
    }


  }

}

</script>


